import React from 'react';
import { Box, Select, FormField, TextInput, Button } from 'grommet';
import Layout from '../components/layout';

let people = [];
class Index extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      people
    };
  }

  onAddName = () => {
    console.log('name added');
    let name = document.getElementById("item").value;
   this.setState({
      people: people.push(name)
    });
    console.log(people);
  } 

  render() {
    return (
      <Layout>
        <Box direction='column' align='center'>
          <Box direction='row'>
            <FormField>
              <TextInput 
                id='item' 
                name='item' 
                placeholder='name'
              />
            </FormField>
            <Box>
            <br />
              <Button 
                    label='Add name'
                    onClick={() => this.onAddName()}
              />
            </Box>
          </Box>
          <br />
          <br />
          <Box direction='row'>
              <Select 
                options={people}
                placeholder="person"
              />
              <Box>
                
              <Button 
                    label='Add drink'
                    onClick={() => this.onAddName()}
              />
            </Box>
          </Box>
          <br />
          <br />
        </Box>
      </Layout>
    );
  }
}

export default Index;



